<template>
	<view class="body">
		<view class="title">{{title}}</view>
		<view class="img">
			<image :src="imgSrc" mode="scaleToFill"></image>
			<view class="videoBtn" v-if="type == 3">
				<image src="data:image/png;base64,"
				 mode="widthFix"></image>
			</view>
		</view>
		<view class="bottom">
			<text class="upName">{{upName}}</text>
			<text class="time">{{time}}</text>
		</view>
	</view>
</template>

<script>
	export default {
		props: ['title', 'imgSrc', 'upName', 'time','type'],
		data() {
			return {

			};
		}
	}
</script>

<style lang="scss" scoped>
	* {
		margin: 0;
	}

	.body {
		position: relative;
		padding: $box-margin-top $box-margin-left;
		width: 750upx;
		box-sizing: border-box;
		min-height: calc(140upx + 48upx);
	}

	.title {
		font-size: $list-title;
		color: $list-title-color;
		line-height: $list-title-line;
		padding-right: 220upx;
		box-sizing: border-box;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2; //需要显示时文本行数
		overflow: hidden;
	}

	.img {
		position: absolute;
		top: $box-margin-top;
		right: $box-margin-left;
		width: 220upx;
		height: 140upx;

		image {
			width: 100%;
			height: 100%;
		}
	}

	.videoBtn {
		position: absolute;
		background-color: rgba(0, 0, 0, 0.3);
		width: 100%;
		height: 100%;
		top: 0;

		image {
			position: absolute;
			left: 50%;
			top: 50%;
			margin-left: -30upx;
			margin-top: -30upx;
			width: 60upx;
			height: 60upx;
		}
	}

	.bottom {
		margin-top: 20upx;
		font-size: $list-info;
		color: $list-info-color;
	}

	.time {
		margin-left: 20upx;
	}
</style>
